import { SPFI, spfi, SPFx } from "@pnp/sp";
import "@pnp/sp/webs";
import "@pnp/sp/lists";
import "@pnp/sp/items";
import "@pnp/sp/site-users/web";
import { WebPartContext } from "@microsoft/sp-webpart-base";
import { IApprovalItem } from "../models/IApprovalItem";

let _sp: SPFI | null = null;

export const getSP = (context: WebPartContext): SPFI => {
  if (_sp === null && context) {
    _sp = spfi().using(SPFx(context));
  }
  return _sp!;
};

export const getApprovalItems = async (context: WebPartContext, filterOption: string): Promise<IApprovalItem[]> => {
  try {
    const sp = getSP(context);
    let filter = "";
    if (filterOption === "AllPendingTasks") {
      filter = "Status eq 'Under Review'";
    } else if (filterOption === "AllTasksAssignedToMeAndMyProxiesFor") {
      filter = "Status ne 'Under Review'";
    }

    const items: any[] = await sp.web.lists
      .getByTitle("ContractingPlan")
      .items
      .filter(filter)
      .select(
        "Id",
        "MasterID",
        "ProgramDes",
        "ProjectID",
        "ProjectName",
        "DateSubmitted",
        "SponsorDepartment",
        "Requester_x0020_Assigned",
        "OCRAnalyst_x0020_Assigned",
        "SponsorProjectManager",
        "Status",
        "Created",
        "Modified"
      )
      .orderBy("Created", false)();

    return items.map((item: any) => ({
      masterId: item.MasterID,
      programDes: item.ProgramDes || "",
      projectId: item.ProjectID || "",
      projectName: item.ProjectName || "",
      dateSubmitted: item.DateSubmitted ? new Date(item.DateSubmitted).toLocaleDateString() : "",
      sponsorDepartment: item.SponsorDepartment || "",
      requesterAssigned: item.Requester_x0020_Assigned || "",
      ocrAnalystAssigned: item.OCRAnalyst_x0020_Assigned || "",
      sponsorProjectManager: item.SponsorProjectManager || "",
      status: item.Status || "",
      created: new Date(item.Created),
      modified: new Date(item.Modified),
      isEditable: item.Status === "Under Review"
    }));
  } catch (error) {
    console.error("Error fetching approval items:", error);
    throw error;
  }
};

export const getCurrentUser = async (context: WebPartContext): Promise<string> => {
  try {
    const sp = getSP(context);
    const currentUser = await sp.web.currentUser();
    return currentUser.Title;
  } catch (error) {
    console.error("Error getting current user:", error);
    throw error;
  }
};
