import * as React from 'react';
import { useState, useEffect } from 'react';
import { DetailsList, SelectionMode, IColumn } from '@fluentui/react/lib/DetailsList';
import { SearchBox } from '@fluentui/react/lib/SearchBox';
import { Dropdown, IDropdownOption } from '@fluentui/react/lib/Dropdown';
import { Spinner, SpinnerSize } from '@fluentui/react/lib/Spinner';
import { MessageBar, MessageBarType } from '@fluentui/react/lib/MessageBar';
import { IApprovalListProps } from './IApprovalListProps';
import { IApprovalItem } from '../models/IApprovalItem';
import { getApprovalItems } from '../services/SPService';
import styles from './ApprovalList.module.scss';

export const ApprovalList: React.FC<IApprovalListProps> = (props) => {
  const [items, setItems] = useState<IApprovalItem[]>([]);
  const [filteredItems, setFilteredItems] = useState<IApprovalItem[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState('');
  const [searchText, setSearchText] = useState('');
  const [selectedFilter, setSelectedFilter] = useState('AllPendingTasks');

  const filterOptions: IDropdownOption[] = [
    { key: 'AllPendingTasks', text: 'All Pending Tasks' },
    { key: 'AllTasksAssignedToMeAndMyProxiesFor', text: 'All Tasks Assigned To Me' }
  ];

  const columns: IColumn[] = [
    {
      key: 'projectName',
      name: 'Project Name',
      fieldName: 'projectName',
      minWidth: 100,
      maxWidth: 200,
      isResizable: true
    },
    {
      key: 'programDes',
      name: 'Program Description',
      fieldName: 'programDes',
      minWidth: 100,
      maxWidth: 200,
      isResizable: true
    },
    {
      key: 'dateSubmitted',
      name: 'Date Submitted',
      fieldName: 'dateSubmitted',
      minWidth: 100,
      maxWidth: 100,
      isResizable: true
    },
    {
      key: 'sponsorDepartment',
      name: 'Sponsor Department',
      fieldName: 'sponsorDepartment',
      minWidth: 100,
      maxWidth: 150,
      isResizable: true
    },
    {
      key: 'requesterAssigned',
      name: 'Requester',
      fieldName: 'requesterAssigned',
      minWidth: 100,
      maxWidth: 150,
      isResizable: true
    },
    {
      key: 'status',
      name: 'Status',
      fieldName: 'status',
      minWidth: 100,
      maxWidth: 100,
      isResizable: true,
      onRender: (item: IApprovalItem) => (
        <div className={`${styles.statusCell} ${item.status === 'Under Review' ? styles.pending : styles.completed}`}>
          {item.status}
        </div>
      )
    }
  ];

  useEffect(() => {
    loadItems();
  }, [selectedFilter]);

  const loadItems = async () => {
    try {
      setIsLoading(true);
      setError('');
      const approvalItems = await getApprovalItems(props.context, selectedFilter);
      setItems(approvalItems);
      setFilteredItems(approvalItems);
    } catch (err) {
      setError('Error loading approval items. Please try again later.');
      console.error('Error:', err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSearch = (newValue?: string) => {
    const searchValue = newValue || '';
    setSearchText(searchValue);
    
    if (!searchValue) {
      setFilteredItems(items);
      return;
    }

    const filtered = items.filter(item => 
      item.projectName.toLowerCase().includes(searchValue.toLowerCase()) ||
      item.programDes.toLowerCase().includes(searchValue.toLowerCase()) ||
      item.sponsorDepartment.toLowerCase().includes(searchValue.toLowerCase())
    );
    setFilteredItems(filtered);
  };

  const handleFilterChange = (event: React.FormEvent<HTMLDivElement>, option?: IDropdownOption) => {
    if (option) {
      setSelectedFilter(option.key as string);
    }
  };

  if (isLoading) {
    return (
      <div className={styles.approvalList}>
        <div className={styles.container}>
          <div className={styles.loadingSpinner}>
            <Spinner size={SpinnerSize.large} label="Loading items..." />
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className={styles.approvalList}>
      <div className={styles.container}>
        <div className={styles.header}>Approval List</div>
        
        {error && (
          <MessageBar messageBarType={MessageBarType.error}>
            {error}
          </MessageBar>
        )}

        <div className={styles.row}>
          <div className={styles.column}>
            <SearchBox
              className={styles.searchBox}
              placeholder="Search by project name, program, or department"
              onChange={(_, newValue) => handleSearch(newValue)}
              value={searchText}
            />
          </div>
        </div>

        <div className={styles.row}>
          <div className={styles.column}>
            <Dropdown
              className={styles.filterDropdown}
              label="Filter"
              selectedKey={selectedFilter}
              options={filterOptions}
              onChange={handleFilterChange}
            />
          </div>
        </div>

        <div className={styles.detailsList}>
          <DetailsList
            items={filteredItems}
            columns={columns}
            selectionMode={SelectionMode.none}
            setKey="set"
            layoutMode={1}
            isHeaderVisible={true}
            onItemInvoked={(item) => {
              if (item.isEditable) {
                // Handle item click for editable items
                console.log('Item clicked:', item);
              }
            }}
          />
          
          {filteredItems.length === 0 && !isLoading && (
            <div className={styles.noResults}>
              No items found
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
